.box{
    background-color: #00113a;
    height: 630px;
    width: 100%;
    padding-top: 170px;
}
.flexhead{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.profilepic{
    border-radius: 50%;
    height: 250px;
    box-shadow: #ccc 20px;
    box-shadow: 0 4px 8px 0 #021b59, 0 6px 20px 0 #021b59;
}

.introduction{
    height: 300px;
    width: 350px;
    color: white;
    text-align: left;
    font-size: 30px;
    padding: 40px;
    text-shadow: #111 20px;
    text-shadow: 0 4px 8px 0 #021b59, 0 6px 20px 0 #021b59;
}
.introduction h2{
    font-weight: 600;
}
.name{
    line-height: 40%;
}
.name a{
    color: white;
    margin-right: 15px;
    font-size: 20px;
    padding-top: 10px;
}
a:hover{
    color: white;
    font-size: 50px;
    box-shadow: #ccc 20px;
    padding-top: 5px;
}
.fa-linkedin:hover{
    color: white;
    box-shadow: #ccc 20px;
    padding-top: 5px;
}
.fa-instagram:hover{
    color: white;
    box-shadow: #ccc 20px;
    padding-top: 5px;
}
p{
    font-size: 18px;
}

.down-arrow{
    width: 37px;
    animation: move 1.1s  infinite ease-in-out;
    display: block;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    overflow: hidden;
}
@keyframes move{
    0%{margin-top: 105px;}
    50%{margin-top: 168px;}
    100%{margin-top: 105px;}
}

.fa {
  }
  
  .fa:hover {
      opacity: 0.7;
  }
  
  .fa-facebook {
    background: #111;
    color: white;
    padding-top: 10px;
  }  
  .fa-linkedin {
    color: white;
    padding-top: 10px;
  }
  .fa-instagram {
    color: white;
    padding-top: 10px;
  }


  